<template>
    <div>
        <form class="form-signin" @submit.prevent="signin">
        <!--                      將登入的事件寫在form上面 -->
            <h1 class="h3 mb-3 font-weight-normal">請先登入</h1>
            <label for="inputEmail" class="sr-only">Email address</label>
            <input type="email" id="inputEmail" class="form-control" placeholder="Email address" v-model="user.username"
                required autofocus>
            <label for="inputPassword" class="sr-only">Password</label>
            <input type="password" id="inputPassword" v-model="user.password" class="form-control"
                placeholder="Password" required>
            <div class="checkbox mb-3">
                <label>
                    <input type="checkbox" value="remember-me"> Remember me
                </label>
            </div>
            <button class="btn btn-lg btn-primary btn-block" type="submit">Sign in</button>
            <p class="mt-5 mb-3 text-muted">&copy; 2017-2018</p>
        </form>
    </div>
</template>

<script>
export default {
    name: 'HelloWorld',
    data() {
        return {
            user: {
                username: '',
                password: '',
            },
        };
    },
    methods: {
        // 加入登入的事件
        signin() {
            const api = `${process.env.APIPATH}/admin/signin`;
            //              伺服器路徑/登入的路徑
            //  admin 將cokies正確存入伺服器中
            const vm = this;
            this.$http.post(api, vm.user).then((response) => {
                // post將用戶的資料載進來 vm.user就是上面的username password
                console.log(response.data);
                if(response.data.success){
                    const token = response.data.token;
                    const expired = response.data.expired;
                    console.log(token, expired)
                    document.cookie = `hexToken=${token}; expires${new Date(expired)};`
                    // 將cookie寫入
                    vm.$router.push('/admin/products');
                }
                //如果登入成功response.data.success
            })
        }
    },
}
</script>

<style scoped>
html,
body {
    height: 100%;
}

body {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: #f5f5f5;
}

.form-signin {
    width: 100%;
    max-width: 330px;
    padding: 15px;
    margin: auto;
}

.form-signin .checkbox {
    font-weight: 400;
}

.form-signin .form-control {
    position: relative;
    box-sizing: border-box;
    height: auto;
    padding: 10px;
    font-size: 16px;
}

.form-signin .form-control:focus {
    z-index: 2;
}

.form-signin input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
}

.form-signin input[type="password"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}
</style>


